import { React, Component } from "react";

class TwitterComponent extends Component {
	constructor(props) {
		super(props);
		this.twitterIni = this.twitterIni.bind(this);
		this.state = {
			loaded: null,
		};
		this.settings = {
			initimeout: 1500,
			timeout: 1000,
		};
	}

	twitterIni() {
		if (document.querySelectorAll(".twitter-tweet").length) {
			if (!window.twttr && this.state.loaded === null) {
				let asyncEmbed = async function () {
					let scriptEl = document.createElement("script");
					scriptEl.defer = "defer";
					scriptEl.src = "https://platform.twitter.com/widgets.js";
					document.body.appendChild(scriptEl);
				};
				asyncEmbed().then((added) => {
					this.setState({ loaded: true }, () => {
						setTimeout(() => {
							let twitterCards = document.querySelectorAll(".twitter-tweet");
							twitterCards.forEach((element) => {
								element.classList.add("mx-auto");
							});
						}, this.settings.initimeout);
					});
				});
			} else if (typeof window.twttr !== "undefined") {
				let asyncLoad = async function () {
					window.twttr.widgets.load();
				};
				asyncLoad().then((loadedscript) => {
					setTimeout(() => {
						let twitterCards = document.querySelectorAll(".twitter-tweet");
						twitterCards.forEach((element) => {
							element.classList.add("mx-auto");
						});
					}, this.settings.timeout);
				});
			}
		}
	}

	componentDidMount() {
		this.twitterIni();
	}

	componentDidUpdate() {
		this.twitterIni();
	}

	render() {
		return <div id="twitterSupport" />;
	}
}

export default TwitterComponent;
